.page-footer {
  background-color: @dark-text-color;

  padding-top: 41px;
  padding-left: 21px;
  padding-right: 21px;
  padding-bottom: 0;
}

.page-footer__wrapper {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  position: relative;
}

.page-footer__logo-image {
  align-self: center;
  width: 174px;
  height: 196px;
}

.page-footer__jscorp-link {
  align-self: flex-end;

  position: absolute;
  bottom: 0px;
  right: 0px;

  display: flex;
  justify-content: flex-end;
}

.page-footer__jscorp-image {
  height: 64px;
  width: 64px;
}

.page-footer__section {
  display: flex;
  flex-direction: column;
}

.section-connect {
  align-items: center;
  padding-top: 26px;
}

.section-connect__image--instagram {
  height: 42px;
  width: 42px;
  padding-bottom: 19px;

  display: block;
}

.section-connect__wrapper,
.section-connect__adress {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.section-connect__adress-item {
  padding-bottom: 12px;
}

.section-connect__adress a {
  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @white-text-color;
  font-size: 32px;
  line-height: 25px;
  font-weight: normal;
}

.section-connect__form {
  display: none;
}

.section-links .section-connect__header {
  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @white-text-color;
  font-size: 30px;
  line-height: 32px;
  font-weight: normal;

  padding-top: 25px;
  padding-bottom: 13px;
}

.section-links a {
  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @light-text-color;
  font-size: 30px;
  line-height: 30px;
  font-weight: normal;
}

.section-links__link {
  padding-bottom: 13px;
}


.section-apps {
  padding-top: 50px;

  align-items: center;
}

.section-apps__mobile-app {
  padding-bottom: 40px;
}

.section-apps__mobile-app-image {
  width: 218px;
  height: auto;
}

.page-footer__legals-list {
  display: flex;
  flex-direction: column;
  padding-top: 30px;

  a {
    font-size: 10px;
    line-height: 33px;
    color: @light-text-color;
  }
}

.page-footer__rights {
  font-size: 12px;
  line-height: 44px;
  color: @light-text-color;
}

.site-container--show-mobile-menu .page-footer {
  display: none;
}


@media (min-width: @desktop-width) {
  .site-container .page-footer {
    display: block;
  }


  .page-footer {
    padding-top: 14px;
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 0;
  }

  .page-footer__wrapper--0 {
    width: @desktop-width;

    margin-left: auto;
    margin-right: auto;

    flex-direction: row;
  }

  .section-apps {
    display: none;
  }

  .section-links__links {
    padding-bottom: 30px;
  }

  .page-footer__wrapper--2,
  .page-footer__legals-list,
  .section-connect__wrapper {
    flex-direction: row;
  }

  .section-connect {
    align-items: flex-start;
    padding-left: 31px;
    padding-right: 81px;
    padding-top: 0;
  }

  .section-connect__wrapper {
    padding-bottom: 26px;
  }

  .page-footer__logo-image {
    width: 121px;
    height: 137px;
  }

  .section-connect__image--instagram {
    height: 29px;
    width: 29px;
    padding-bottom: 0;
  }

  .section-connect__adress {
    align-items: flex-start;
  }

  .section-connect__adress-item {
    padding-bottom: 3px;
    padding-left: 20px;
    line-height: 13px;
  }

  .section-connect__adress a {
    font-size: 13px;
    line-height: 0;
  }

  .section-connect__form {
    display: flex;
    flex-direction: column;
  }

  .section-connect__form-title {
    font-family: "Franxurter Totally", "Arial", sans-serif;
    color: @white-text-color;
    font-size: 17px;
    line-height: 14px;
    font-weight: normal;

    padding-bottom: 8px;
  }

  .section-connect__form-input-wrapper {
    display: flex;
    align-items: center;

    padding-bottom: 13px;
  }

  .section-connect__form-input-email {
    border: 1px solid @background-blue-color;
    border-radius: 10px;

    background-color: @background-white-color;

    font-size: 10px;
    line-height: 33px;

    padding-left: 9px;
    padding-right: 9px;

    margin-right: 19px;

    width: 193px;
  }

  .section-connect__form-button {
    font-family: "Franxurter Totally", "Arial", sans-serif;
    color: @dark-text-color;
    font-size: 18px;
    line-height: 29px;
    font-weight: normal;

    background-color: @background-blue-color;
    border: none;
    border-radius: 10px;

    box-shadow: 0 3px 6px rgba(0,0,0,0.16);
    padding: 4px 40px;

    cursor: pointer;
  }


  .section-connect__form-input-wrapper {
    display: flex;
    align-items: center;
  }

  .section-connect__form-checkbox {
    margin-right: 7px;
  }

  .section-connect__form-checkbox-label,
  .section-connect__form-checkbox-label a {
    color: @light-text-color;
    font-size: 10px;
    line-height: 8px;
  }

  .section-connect__form-checkbox-label a {
    text-decoration: underline;
  }


  .section-links {
    padding-right: 47px;
  }

  .section-links .section-connect__header {
    font-size: 20px;
    line-height: 21px;

    padding-top: 0;
    padding-bottom: 3px;
  }

  .section-links a {
    font-size: 20px;
    line-height: 21px;
  }

  .section-links__link {
    padding-bottom: 8px;
  }

  .page-footer__legals-list {
    padding: 0;
    position: absolute;
    bottom: 9px;
    right: -150px;
  }

  .page-footer__legals-item {
    font-size: 9px;
    line-height: 11px;

    padding-top: 1px;
    padding-bottom: 1px;
    padding-right: 5px;
    padding-left: 5px;
    border-right: 1px solid @light-text-color;

    a {
      font-size: 9px;
      line-height: 11px;
    }
  }

  .page-footer__legals-item:last-child {
    border-right: none;
  }

  .page-footer__rights {
    font-size: 9px;
    line-height: 11px;

    padding-top: 1px;
    padding-bottom: 1px;
    position: absolute;
    bottom: 9px;
    left: 50%;

    a {
      font-size: 9px;
      line-height: 11px;
    }
  }


  .page-footer__jscorp-image:hover {
    border-bottom: 15px solid #25C1E8;
  }

  .page-footer__jscorp-image {
    height: 36px;
    width: 36px;
  }


}
