.about__article {
  font-family: "Franxurter Totally", "Arial", sans-serif;

  background-color: @background-blue-color;
  border-radius: 16px;
  box-shadow: 0 0 18px rgba(0,0,0,0.18);

  margin-top: 15px;

  padding-top: 42px;
  padding-left: 8px;
  padding-right: 8px;
  padding-bottom: 0;
}

.article {
  display: flex;
  flex-direction: column;
}

.article__data {
  display: flex;
  flex-direction: column;
}

.article__left-side {
  display: flex;
  flex-direction: column;
}


.article__header {
  // font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @background-white-color;
  font-size: 35px;
  // line-height: 47px;
  font-weight: normal;

  padding-bottom: 5px;
}

.article__header-text {
  // font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 20px;
  line-height: 25px;
  font-weight: normal;

  padding-bottom: 25px;
}

.article__header-mobile {
  display: none;
  color: @background-white-color;
  font-size: 46px;
  line-height: 32px;
  font-weight: normal;
}

.article__data-header {
  // font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @background-white-color;
  font-size: 30px;
  line-height: 25px;
  font-weight: normal;

  padding-bottom: 10px;
}

.article__data-list {
  padding-bottom: 25px;
}

.article__data-item {
  // font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 20px;
  // line-height: 47px;
  font-weight: normal;

  list-style-type: "- ";

  padding-bottom: 10px;
  margin-left: 14px;
}


.article__data-type-list {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding-top: 34px;
  padding-bottom: 54px;
}

.article__data-type-item {
  font-size: 25px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.article__data-type-image {
  padding-bottom: 10px;
}

.article__data-founder {
  font-family: "Gotham Pro", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: bold;
  color: @white-text-color;
  text-align: center;
  padding-bottom: 66px;
}

.article__data-image {
  align-self: center;
  height: 424px;
  width: 319px;
  text-shadow: 0 3px 8px rgba(0,0,0,0.25);
}






@media (min-width: @desktop-width) {
  .about__article {
    padding-top: 41px;
    padding-left: 30px;
    padding-right: 0;
    padding-bottom: 0;

    flex: 0 0 844px;
    box-sizing: border-box;

    margin-top: 20px;

  }

  .article {
    // width: 844px;


    background-image: url("../img/logo-backround.png");

    // @media (min-resolution: @retina-dppx) {
    //   background-image: url("../img/logo-backround@2x.png");
    // }

    background-position: left;
    background-repeat: no-repeat;
    background-size: contain;
  }

  .article__header {
    display: none;
  }

  .article__header-mobile {
    display: inline;
  }

  .article__header-text {
    line-height: 32px;
    padding-bottom: 28px;
  }

  .article__data-header {
    padding-bottom: 23px;
  }

  .article__data-header--center {
    text-align: center;
    padding-bottom: 28px;
  }

  .article__data {
    flex-direction: row;
  }

  .article__data-list {
    padding-bottom: 24px;
  }

  .article__data-item {
    padding-bottom: 14px;
    margin-left: 14px;
  }

  .article__data-type-list {
    padding-top: 0;
    padding-bottom: 30px;

  }

  .article__data-type-image {
    padding-bottom: 5px;
  }

  .article__data-image {
    // padding-top: 5px;
    padding-right: 16px;
    height: 473px;
    width: 356px;
  }

  .article__data-founder {
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 0;
  }
}

