.contact-us-form {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 60;

  background-color: rgba(123, 123, 123, 0.5);

  // display: flex;
  // justify-content: center;
  // align-items: center;

  *:focus {
    outline: none;
  }
}

.contact-us-form__overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(123, 123, 123, 0.5);
  z-index: 60;
}

.contact-us-form__wrapper {
  z-index: 61;
  position: absolute;
  margin: auto;
  // left: calc(10%);
  top: 0; left: 0; bottom: 0; right: 0;

  box-sizing: border-box;
  background-color: @background-white-color;
  border-radius: 10px;
  box-shadow: 0 0 13px rgba(0,0,0,0.44);

  width: 80%;
  height: 552px;

  padding-top: 18px;
  padding-bottom: 31px;
  padding-left: 34px;
  padding-right: 34px;

  // margin-left: auto;
  // margin-right: auto;
  outline: none;
}

.contact-us-form__header {
  padding-bottom: 21px;
}

.contact-us-form__form,
.contact-us-form__form-input-wrapper {
  display: flex;
  flex-direction: column;
}

.contact-us-form__form {
height: 458px;
}

.contact-us-form__form-input-wrapper--checkbox {
  flex-direction: row;

  padding-bottom: 12px;
}

.contact-us-form__form-input {
  border: 1px solid @background-blue-color;
  border-radius: 10px;

  font-size: 15px;
  line-height: 30px;

  padding: 4px 11px;

  margin-bottom: 8px;
}

.contact-us-form__form-checkbox {
  margin-right: 11px;
  margin-top: 3px;
}

.contact-us-form__form-checkbox-label,
.contact-us-form__form-checkbox-label a {
  color: @light-text-color;
  font-size: 15px;
  line-height: 16px;
}

.contact-us-form__form-checkbox-label a {
  text-decoration: underline;
}



.contact-us-form__form-input-comment {
  resize: none;

  flex-grow: 1;
  flex-shrink: 1;
}

.contact-us-form__form-input-select {
  display: none;
  transform-origin: bottom;
  animation: bounce 0.3s;
  overflow: hidden;

}

.contact-us-form__form-input-wrapper--select {
  position: relative;
}

.contact-us-form__form-input-wrapper--select::before {
  position: absolute;
  top: 16px;
  right: 18px;
  content: "";
  background-image: url("../img/down.svg");
  height: 9px;
  width: 15px;
  background-position: right;
  background-repeat: no-repeat;
  background-size: contain;
}

.contact-us-form__form-input-theme {
  cursor: pointer;
}


