.contact-us-form__form-input-wrapper--select:focus-within > .contact-us-form__form-input-select {//},
// .contact-us-form__form-input-select:hover,
// .contact-us-form__form-input-select:focus-within + .contact-us-form__form-input-select {
    display: block;
    transform-origin: top;
    animation: bounce 0.3s;
  }


.contact-us-form__form-input-select-option:checked {
  background: @background-blue-color linear-gradient(0deg, @background-blue-color 0%, @background-blue-color 100%);
  border-radius: 5px;
}

.contact-us-form__form-button {
  align-self: center;

  background-color: @background-blue-color;
  border: none;
  border-radius: 10px;
  box-shadow: 0 3px 6px rgba(6,6,7,0.31);

  width: 100%;

  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 20px;
  line-height: 22px;
  font-weight: normal;
  text-transform: uppercase;

  padding-top: 9px;
  padding-bottom: 9px;

  cursor: pointer;
}


@media (min-width: @desktop-width) {
  .contact-us-form__wrapper {
    width: 500px;

    padding-left: 103px;
    padding-right: 103px;
    padding-top: 18px;
    padding-bottom: 31px;


  }
}
