.order-head {
  background-color: #FFFFFF;
  border-radius: 16px;
  box-shadow: 0 0 18px rgba(0,0,0,0.05);
  display: flex;
  flex-direction: column-reverse;
  align-items: stretch;

  padding-top: 28px;
  padding-bottom: 18px;
  padding-left: 13px;
  padding-right: 10px;
}

.order-head__right-panel {
  display: flex;
  justify-content: center;
  align-items: center;
}

.order-head__header {
  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 30px;
  line-height: 30px;
  font-weight: normal;

  padding-bottom: 3px;
}

.order-head__text {
  color: @light-text-color;
  font-size: 24px;
  line-height: 33px;

  padding-bottom: 15px;
}

.order-head__term {
  color: @light-text-color;
  font-size: 20px;
  line-height: 33px;

  padding-bottom: 12px;

  :first-child {
    padding-bottom: 0;
  }
}

.order-head__term-dd {
  color: @dark-text-color;
  font-weight: bold;
}

.order-head__text-term {
  color: @dark-text-color;
  font-size: 12px;
  line-height: 18px;
}


.order-head__logo-image--online-cooking {
  padding-bottom: 17px;

  height: 288px;
  width: 323px;
}

.order-head__logo-image--no-gluten {
  padding-bottom: 20px;
  padding-top: 20px;

  height: 139px;
  width: 323px;
}

.order-head__logo-image--catering {
  padding-bottom: 17px;

  height: 300px;
  width: 318px;
}

.order-head__logo-image--foods {
  padding-bottom: 17px;

  height: 284px;
  width: 288px;
}


@media (min-width: @desktop-width) {
  .order-head {
    flex-direction: row;
    padding-top: 48px;
    padding-bottom: 21px;
    padding-left: 50px;
    padding-right: 50px;
  }

  .order-head__right-panel {
    width: 40%;
  }

  .order-head__left-panel {
    // border: 1px solid red;
    width: 60%;
  }

  .order-head__header {
    font-size: 64px;
    line-height: 58px;

    padding-bottom: 8px;
  }

  .order-head__text {
    padding-bottom: 24px;
    padding-right: 90px;
  }

  .order-head__term {
    padding-bottom: 7px;
  }

  .order-head__term-dd {
    // padding-bottom: 18px;
  }

  .order-head__term-dd br {
    display: none;
  }

  .order-head__text-term {
    line-height: 35px;
  }

  .order-head__text-term br {
    display: none;
  }

  .order-head__logo-image--online-cooking {
    padding: 0;

    height: 335px;
    width: 376px;
  }

  .order-head__logo-image--no-gluten {
    padding: 0;

    height: 242px;
    width: 561px;
  }

  .order-head__logo-image--catering {
    padding-bottom: 0;

    height: 345px;
    width: 367px;
  }

  .order-head__logo-image--foods {
    padding-bottom: 0;

    height: 327px;
    width: 324px;
  }
}
