.page-header__wrapper {
  // width: @desktop-width;
  // width: 100%;
  margin-left: auto;
  margin-right: auto;

  padding-left: 20px;
  padding-right: 20px;

  box-shadow: 0 2px 7px rgba(0,0,0,0.23);

  display: flex;
  flex-direction: row;
  justify-content: space-between;

  a {
    font-size: 22px;
    line-height: 17px;
  }
}

.page-header__mobile-menu-button {
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
}

.page-header__logo {
  padding-top: 8px;
  padding-bottom: 8px;
}



.page-header__basket {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.page-header__basket-image {
  margin-left: 6px;
}

@import "./main-menu.less";


.page-header__nav,
.page-header__phone {
  display: none;
}

@media (min-width: @desktop-width) {
  .page-header__wrapper {
    width: @desktop-width;
    box-shadow: none;
    padding-left: 0;
    padding-right: 0;
  }

  .page-header__mobile-menu-button {
    display: none;
  }

  .page-header__nav,
  .page-header__phone {
    display: flex;
  }

  .page-header__logo {
    margin-right: 80px;
  }

  .page-header__nav {
    display: flex;
    align-items: stretch;
  }

  .page-header__phone {
    margin-left: auto;
    margin-right: 46px;

    display: flex;
    align-items: center;
  }
}
