@font-face {
  font-family: "Gotham Pro";
  font-style: normal;
  font-weight: 400;
  src: local("Gotham Pro"), local("Gotham Pro Regular"), url("../fonts/GothamPro.woff") format("woff");
}
@font-face {
  font-family: "Gotham Pro";
  font-style: bold;
  font-weight: 700;
  src: local("Gotham Pro"), local("Gotham Pro Bold"), url("../fonts/GothamPro-Bold.woff") format("woff");
}
@font-face {
  font-family: "Gotham Pro";
  font-style: medium;
  font-weight: 500;
  src: local("Gotham Pro"), local("Gotham Pro Medium"), url("../fonts/GothamPro-Medium.woff") format("woff");
}
@font-face {
  font-family: "Franxurter Totally";
  font-style: medium;
  font-weight: 500;
  src: local("Franxurter Totally"), local("Franxurter Totally Medium"), url("../fonts/FranxurterTotallyMedium.woff") format("woff");
}
html {
  scroll-behavior: smooth;
}
body {
  background-color: #FFFFFF;
  font-family: "Gotham Pro", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  margin: 0;
  padding: 0;
}
p,
ul,
h1,
h2,
h3 {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  cursor: pointer;
}
li {
  list-style-type: none;
}
.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  border: 0;
}
/* Размеры скроллбара */
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
/* Трек поле скроллбара */
::-webkit-scrollbar-track {
  background: none;
}
/* Ползунок скроллбара */
::-webkit-scrollbar-thumb {
  background: none;
  border: 3px solid #25C1E8;
  border-radius: 3px;
}
.page-header {
  background-color: #25C1E8;
  box-shadow: none;
  z-index: 10;
  position: fixed;
  width: 100%;
}
.page-header a {
  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: #1B222C;
}
.page-header__wrapper {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  box-shadow: 0 2px 7px rgba(0, 0, 0, 0.23);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.page-header__wrapper a {
  font-size: 22px;
  line-height: 17px;
}
.page-header__mobile-menu-button {
  border: none;
  outline: none;
  background: none;
  cursor: pointer;
}
.page-header__logo {
  padding-top: 8px;
  padding-bottom: 8px;
}
.page-header__basket {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.page-header__basket-image {
  margin-left: 6px;
}
.main-menu {
  display: flex;
  align-items: stretch;
}
.main-menu__item {
  margin-right: 26px;
  position: relative;
  display: flex;
  align-items: stretch;
}
.main-menu__item a {
  display: flex;
  align-items: center;
  padding-right: 10px;
  padding-left: 10px;
  font-size: 20px;
  line-height: 21px;
}
.sub-menu {
  display: none;
  width: 300px;
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 20;
  background-color: #25C1E8;
}
.sub-menu a {
  color: #FFFFFF;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 15px;
  padding-right: 55px;
}
@keyframes bounce {
  0% {
    transform: scaleY(0);
  }
  100% {
    transform: scaleY(1);
  }
}
.main-menu__item:hover > .main-menu__sub-menu {
  display: block;
  transform-origin: top;
  animation: bounce 0.3s;
}
.sub-menu__item:hover {
  text-decoration: underline;
  cursor: pointer;
}
.page-header__nav,
.page-header__phone {
  display: none;
}
@media (min-width: 1276px) {
  .page-header__wrapper {
    width: 1276px;
    box-shadow: none;
    padding-left: 0;
    padding-right: 0;
  }
  .page-header__mobile-menu-button {
    display: none;
  }
  .page-header__nav,
  .page-header__phone {
    display: flex;
  }
  .page-header__logo {
    margin-right: 80px;
  }
  .page-header__nav {
    display: flex;
    align-items: stretch;
  }
  .page-header__phone {
    margin-left: auto;
    margin-right: 46px;
    display: flex;
    align-items: center;
  }
}
.page-header__mobile-menu {
  display: none;
  flex-direction: column;
  flex-grow: 1;
  height: calc(100vh - 103px);
  padding-top: 41px;
  padding-left: 21px;
  padding-right: 21px;
  overflow: auto;
}
.page-header__mobile-menu a {
  color: #FFFFFF;
}
.mobile-menu__mobile-nav a {
  font-size: 30px;
  line-height: 32px;
}
.mobile-nav__menu {
  display: flex;
  flex-direction: column;
}
.mobile-nav__item {
  padding-bottom: 20px;
}
.mobile-menu__mobile-apps {
  display: flex;
  flex-direction: column;
  margin-top: auto;
}
.mobile-menu__mobile-app {
  padding-bottom: 35px;
}
.mobile-menu__mobile-app:first-child {
  padding-bottom: 25px;
}
.mobile-menu__mobile-phone {
  font-size: 42px;
  line-height: 32px;
  padding-bottom: 40px;
}
.mobile-menu__mobile-jscorp {
  line-height: 0 !important;
  align-self: flex-end;
}
.site-container--show-mobile-menu .page-header__mobile-menu {
  display: flex;
}
@media (min-width: 1276px) {
  .site-container .page-header__mobile-menu {
    display: none;
  }
}
@media (min-width: 1276px) {
  .page-header {
    z-index: 50;
    box-shadow: 0 2px 7px rgba(0, 0, 0, 0.23);
  }
}
.page-main {
  padding-top: 85px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 45px;
  background-color: #FAFAFA;
}
.page-main__wrapper {
  margin-left: auto;
  margin-right: auto;
}
.site-container--show-mobile-menu .page-main {
  display: none;
}
