.page-main {
  // padding-top: 20px;
  padding-top: 85px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 45px;

  background-color: #FAFAFA;
}

.page-main__wrapper {
  margin-left: auto;
  margin-right: auto;
}

.site-container--show-mobile-menu .page-main {
  display: none;
}

@import "./head.less";
@import "./links.less";
@import "./about.less";

@import "./order-head.less";
@import "./order-navigator.less";
@import "./menu-group-navigator.less";
@import "./cart.less";
@import "./order-details.less";
@import "./shef-service.less";

@import "./contact-form.less";
@import "./info-window.less";
@import "./policy-window.less";
@import "./question-window.less";
@import "./error-message.less";

@media (min-width: @desktop-width) {
  .site-container .page-main {
    display: block;
  }

  .page-main {
    padding-top: 98px;
    padding-left: 45px;
    padding-right: 45px;
    padding-bottom: 20px;
  }

  .page-main__wrapper {
    width: @desktop-width;
  }
}
