.shef-service {

}




.shef-service__about {
  background-color: #FFFFFF;
  border-radius: 16px;
  box-shadow: 0 0 18px rgba(0,0,0,0.05);

  margin-top: 20px;

  padding-top: 64px;
  padding-left: 64px;
  padding-right: 35px;
  padding-bottom: 0;

  display: flex;
  flex-direction: column;
  align-items: center;
}



.shef-service__text-panel {
  position: relative;

  display: flex;
  flex-direction: column;

  padding-bottom: 20px;
}

.shef-service__text-panel::before {
  position: absolute;

  content: "";

  left: -30px;
  top: -30px;

  background-image: url("../img/commas.png");
  height: 74px;
  width: 22px;
  background-position: right;
  background-repeat: no-repeat;
  background-size: contain;
}

.shef-service__text {
  color: @dark-text-color;
  font-size: 16px;
  line-height: 24px;
  font-weight: normal;
}

.shef-service__text--bold {
  font-size: 19px;
  font-weight: bold;

  padding-bottom: 8px;
}

.shef-service__text--comment {
  font-size: 8px;
  font-weight: bold;
  line-height: 11px;
  color: #A6B6CC;

  width: 145px;
  margin-left: auto;

  padding-top: 20px;
}




.shef-service__photo-panel {
  background-image: url("../img/logo-backround.png");
  background-position: right 0 bottom 0;
  background-repeat: no-repeat;
  background-size: auto 100%;
}

.shef-service__image {
  display: block;

  // padding-right: 120px;
  padding-right: 60px;
  padding-left: 60px;
}






@media (min-width: @desktop-width) {
  .shef-service__about {
    flex-direction: row;

    padding-left: 320px;
    padding-right: 210px;

    padding-top: 45px;
  }

  .shef-service__text-panel::before {

    left: -30px;
    top: -22px;
  }

  .shef-service__text {
    font-size: 22px;
    line-height: 32px;
  }

  .shef-service__text--bold {
    font-size: 26px;
    line-height: 37px;
    font-weight: bold;

    padding-bottom: 8px;
  }

  .shef-service__text--comment {
    font-size: 11px;
    line-height: 16px;
    color: #A6B6CC;

    width: 200px;
  }

  .shef-service__photo-panel {
    padding-left: 45px;
  }

  .shef-service__image {
    padding-right: 150px;
  }

}



