.shef-service__delivery {
  padding-top: 35px;
  padding-bottom: 25px;

  // display: flex;
  // flex-direction: column;
  // align-items: center;
}

.our-delivery__header {
  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 35px;
  line-height: 50px;
  font-weight: normal;
  text-align: center;

  padding-bottom: 26px;
}

.our-delivery__list-wrapper {


  display: flex;


}

.our-delivery__list {
  overflow-x: auto;

  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;

  padding-bottom: 5px;
}

.delivery-item {
  background-color: #FFFFFF;
  border-radius: 16px;
  box-shadow: 0 0 18px rgba(0,0,0,0.05);

  margin-right: 30px;

  padding: 36px;
  padding-bottom: 44px;

  display: flex;
  flex-direction: column;
  align-items: center;

  box-sizing: border-box;
  min-width: 218px;
}

.delivery-item:first-child {
  margin-left: auto;
}

.delivery-item:last-child {
  margin-right: auto;
}


.delivery-item__image {
  height: 120px;
  width: auto;

  padding-bottom: 19px;
}

.delivery-item__text {
  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 25px;
  line-height: 25px;
  font-weight: normal;
}


@media (min-width: @desktop-width) {
  .shef-service__delivery {
    padding-bottom: 88px;
  }
}
