.about {
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.about__connect {
  display: flex;
  flex-direction: column;
}

.about__connect-item {
  display: none;
}

.about__form {
  background-color: #FFFFFF;
  border-radius: 16px;
  box-shadow: 0 0 18px rgba(0,0,0,0.05);
  box-sizing: border-box;

  // margin-right: 20px;
  margin-top: 20px;
  // margin-bottom: 5px;

  padding-left: 32px;
  padding-right: 32px;
  padding-top: 10px;
  padding-bottom: 21px;

  display: flex;
  flex-direction: column;

  .contact-form__title {
    text-align: left;
    font-size: 29px;
    line-height: 33px;
    font-weight: bold;

    padding-bottom: 39px;
  }

  .contact-form__image {
    align-self: center;
    padding-top: 29px;
    padding-bottom: 34px;
  }

  .contact-form__input {
    border: 1px solid @background-blue-color;
    border-radius: 8px;

    font-size: 16px;
    line-height: 21px;

    padding: 14px 23px;
    margin-bottom: 20px;
  }

  .contact-form__input-wrapper {
    margin-top: 5px;
    display: flex;
    align-items: flex-start;
  }

  .contact-form__checkbox-label {
    padding-left: 7px;

    font-size: 15px;
    line-height: 17px;
    color: @dark-text-color;

    a {
      color: @dark-text-color;
      font-size: 15px;
      line-height: 17px;
      text-decoration: underline;
    }
  }

  .contact-form__button {
    box-shadow: 0 3px 6px rgba(0,0,0,0.16);
    border: none;
    border-radius: 8px;
    background-color: @background-blue-color;

    font-family: "Franxurter Totally", "Arial", sans-serif;
    color: @dark-text-color;
    font-size: 19px;

    margin-top: 61px;
    padding-top: 17px;
    padding-bottom: 17px;

    cursor: pointer;
  }
}

@import "./about-article.less";




@media (min-width: @desktop-width) {
  .about {
    flex-direction: row-reverse;

  }

  .about__connect {
    margin-right: 20px;
    width: 412px;
  }

  .about__connect-item {
    transition: 0.4s;
    background-color: #FFFFFF;
    border-radius: 16px;
    box-shadow: 0 0 18px rgba(0,0,0,0.05);

    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
    box-sizing: border-box;
    width: 412px;

    // margin-right: 20px;
    margin-top: 20px;
    // margin-bottom: 5px;

    padding-left: 29px;
    padding-right: 20px;
    padding-top: 13px;
    padding-bottom: 16px;

    .about__connect-image {
      width: 131px;
      height: 134px;
      padding-top: 12px;
      padding-right: 26px;

      path {
        fill: #5E287D;
      }
    }

    .about__connect-item-wrapper {
      display: flex;
      flex-direction: column;
      flex-grow: 1;
      align-self: stretch;
      padding-right: 9px;
    }

    .about__connect-item-header {
      font-family: "Franxurter Totally", "Arial", sans-serif;
      color: @dark-text-color;
      font-size: 18px;
      line-height: 23px;
      font-weight: normal;

      padding-top: 11px;
      padding-bottom: 4px;
    }

    .about__connect-text {
      color: @light-text-color;
      font-size: 14px;
      line-height: 24px;

      padding-bottom: 37px;
    }

    .about__connect-button {
      margin-top: auto;
      border: none;
      background-color: #5E287D;

      width: 130px;
      border-radius: 8px;
      box-shadow: 0 3px 6px rgba(0,0,0,0.16);
      // width: 100%;
      text-align: center;

      font-family: "Franxurter Totally", "Arial", sans-serif;
      color: @white-text-color;
      font-size: 12px;
      line-height: 17px;
      font-weight: normal;

      padding-top: 7px;
      padding-bottom: 7px;
    }
  }

  .about__connect-item:hover {
    background-color: #5E287D;
    transition: 0.4s;

    .about__connect-item-header, .about__connect-text {
      color: @white-text-color;
      transition: 0.4s;
    }

    .about__connect-image {
      path {
        fill: #FFFFFF;
        transition: 0.4s;
      }
    }

    .about__connect-button {
      background-color: @white-text-color;
      color: @dark-text-color;
      box-shadow: none;
      transition: 0.4s;
    }
  }



  .about__connect .about__form {
    display: none;
  }



  .about__form {
    padding-left: 35px;
    padding-right: 16px;
    padding-top: 12px;
    padding-bottom: 38px;

    display: flex;
    flex-direction: column;

    .contact-form__image {
      display: block;
      align-self: center;
      padding-top: 56px;
      padding-bottom: 20px;
    }

    .contact-form__title {
      text-align: center;
      font-size: 34px;
      line-height: 33px;
      font-weight: bold;

      padding-bottom: 43px;
    }

    .contact-form__input {
      border: 1px solid @background-blue-color;
      border-radius: 8px;

      font-size: 16px;
      line-height: 21px;

      padding: 11px 23px;
      margin-bottom: 21px;
    }

    .contact-form__input-wrapper {
      margin-top: 0;
      display: flex;
      align-items: flex-start;
    }

    .contact-form__checkbox-label {
      padding-left: 11px;

      font-size: 15px;
      line-height: 17px;
      color: @dark-text-color;

      a {
        color: @dark-text-color;
        font-size: 15px;
        line-height: 17px;
        text-decoration: underline;
      }
    }

    .contact-form__button {
      box-shadow: 0 3px 6px rgba(0,0,0,0.16);
      border: none;
      border-radius: 8px;
      background-color: @background-blue-color;

      font-family: "Franxurter Totally", "Arial", sans-serif;
      color: @dark-text-color;
      font-size: 19px;

      margin-top: 49px;
      padding-top: 12px;
      padding-bottom: 12px;
    }
  }

  .links__item--contact-form .about__form {
    margin: 0;
    padding: 0;
    // background-color: none;
    // border-radius: 16px;
    box-shadow: none;
    box-sizing: border-box;

    // border: 1px solid red;

    display: flex;
    flex-direction: row;

    .contact-form__image {
      padding: 0;
      padding-right: 50px;
      width: 111px;
      height: 112px;
    }

    .contact-form__title {
      text-align: left;
      font-size: 15px;
      line-height: 33px;
      font-weight: bold;

      padding-bottom: 20px;
    }

    .contact-form__input {
      border: 1px solid @background-blue-color;
      border-radius: 8px;

      font-size: 12px;
      line-height: 21px;

      padding: 11px 23px;
      margin-bottom: 21px;

      margin-right: 15px;

      box-sizing: border-box;
    }

    .contact-form__input-wrapper {
      margin-top: 0;
      display: flex;
      align-items: flex-start;
    }

    .contact-form__input-wrapper--main {
      flex-direction: column;
    }

    .contact-form__checkbox {
      align-self: center;
    }

    .contact-form__checkbox-label {
      text-align: left;
    }

    .contact-form__button {
      margin-top: 0;
      padding-top: 14px;
      padding-bottom: 15px;
      padding-left: 35px;
      padding-right: 35px;

      font-size: 14px;
      font-weight: 400;

      box-sizing: border-box;
    }
  }


}
