.shopping {
  padding-bottom: 115px;
}

.shopping__item {
  position: relative;

  background-color: #F7F7FC;
  border-radius: 16px;

  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 5px;
  padding-right: 24px;

  margin-bottom: 28px;

  display: flex;
  flex-direction: row;
}

.shopping__delete {
  position: absolute;
  top: -8px;
  right: -2px;
}

.shopping__wrapper--1 {
  flex-grow: 1;
}

.shopping__wrapper--3 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.shopping__image {
  width: 71px;
  height: auto;

  object-fit: contain;

  padding-right: 7px;
}

.shopping__weight {
  display: none;
}

.shopping__name {
  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 20px;
  line-height: 17px;
  font-weight: normal;

  padding-bottom: 13px;
}

.shopping__price {
  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 30px;
  line-height: 33px;
  font-weight: normal;
}

@media (min-width: @desktop-width) {
  .shopping {
    padding-bottom: 0;
  }

  .shopping__item {
    align-items: center;
  }

  .shopping__image {
    width: 57px;
    padding-right: 26px;
  }

  .shopping__wrapper {
    display: flex;
    flex-direction: row;
  }

  .shopping__wrapper--1 {
    align-items: center;
  }

  .shopping__wrapper--2 {
    flex-direction: column;
    flex: 0 0 315px;
    box-sizing: border-box;

    padding-right: 15px;
  }

  .shopping__name {
    padding-bottom: 0;
  }

  .shopping__weight {
    display: block;
  }

  .shopping__price {
    margin-left: 215px;
  }



  .shopping__delete {
    position: initial;
  }


}
