.page-header__mobile-menu {
  display: none;
  flex-direction: column;
  flex-grow: 1;
  height: calc(100vh - 103px);

  padding-top: 41px;
  padding-left: 21px;
  padding-right: 21px;

  overflow: auto;
}

.page-header__mobile-menu a {
  color: @white-text-color;
}

.mobile-menu__mobile-nav a {
  font-size: 30px;
  line-height: 32px;
}

.mobile-nav__menu {
  display: flex;
  flex-direction: column;
}

.mobile-nav__item {
  padding-bottom: 20px;
}


.mobile-menu__mobile-apps {
  display: flex;
  flex-direction: column;
  margin-top: auto;
}

.mobile-menu__mobile-app {
  padding-bottom: 35px;

  &:first-child {
    padding-bottom: 25px;
  }
}

.mobile-menu__mobile-phone {
  font-size: 42px;
  line-height: 32px;
  padding-bottom: 40px;
}

.mobile-menu__mobile-jscorp {
  line-height: 0 !important;
  align-self: flex-end;
}

.site-container--show-mobile-menu .page-header__mobile-menu {
  display: flex;
}


// .page-header__mobile-menu--hide {
//   display: none;
// }

@media (min-width: @desktop-width) {
  .site-container .page-header__mobile-menu {
    display: none;
  }
}


