.links {
  // border: 1px solid red;;
}

.links__list {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  overflow-x: auto;
}



.links__item {
  background-color: #FFFFFF;
  border-radius: 16px;
  box-shadow: 0 0 18px rgba(0,0,0,0.05);
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  margin-right: 20px;
  margin-top: 20px;
  margin-bottom: 5px;

  padding-top: 40px;
  padding-bottom: 30px;
  padding-left: 18px;
  padding-right: 18px;

  box-sizing: border-box;
  flex: 0 0 373px;
}

.links__item--contact-form {
  display: none;
}

.links__image {
  padding-bottom: 23px;
}

.links__item-wrapper {
  // border: 1px solid red;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  align-self: stretch;
}

.links__item-header {
  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 25px;
  line-height: 30px;
  font-weight: normal;

  padding-bottom: 26px;
}

.links__text {
  color: @light-text-color;
  font-size: 18px;
  line-height: 30px;

  padding-bottom: 44px;
}

.links__button {
  margin-top: auto;
  border: none;
  background-color: @background-blue-color;
  border-radius: 8px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16);
  width: 100%;

  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 19px;
  line-height: 19px;
  font-weight: normal;

  padding-top: 17px;
  padding-bottom: 17px;
}

.links__image {
  width: auto;
  height: 210px;
}

.links__item--puprle .links__button {
  background-color: #5E287D;
  color: @white-text-color;
}

.links__image--image7 {
  path {
    fill: #5E287D;
  }
}


@media (min-width: @desktop-width) {
  .links__list {
    flex-wrap: wrap;
  }

  .links__item {
    flex-direction: row-reverse;
    flex: 0 0 412px;
    padding-left: 30px;
    padding-right: 20px;
    padding-top: 16px;
    padding-bottom: 16px;
    margin-bottom: 0;

    transition: 0.4s;
  }

  .links__item--contact-form {
    display: flex;
    flex: 0 0 844px;
    justify-content: center;

    padding-top: 44px;
    padding-bottom: 35px;
  }





  .links__list {
    li:nth-child(3n),
    li:last-child {
      margin-right: 0;
    }
  }

  .links__button {
    margin-top: 5px;
    width: 130px;
    font-size: 12px;
    padding-top: 7px;
    padding-bottom: 7px;
  }



  .links__item--hovered:hover {
    background-color: @background-blue-color;
    transition: 0.4s;

    .links__item-header, .links__text {
      color: @white-text-color;
      transition: 0.4s;
    }

    .links__button {
      background-color: @white-text-color;
      color: @dark-text-color;
      box-shadow: none;
      transition: 0.4s;
    }
  }

  .links__item--puprle:hover {
    background-color: #5E287D;

    path {
      fill: #FFFFFF;
      transition: 0.4s;
    }

    .links__item-header, .links__text {
      color: @white-text-color;
      transition: 0.4s;
    }

    .links__button {
      background-color: @white-text-color;
      color: @dark-text-color;
      box-shadow: none;
      transition: 0.4s;
    }
  }

  .links__item-wrapper {
    padding-right: 9px;
  }

  .links__item-header {
    font-size: 18px;
    line-height: 23px;
    text-align: left;

    padding-top: 6px;
    padding-bottom: 4px;
  }

  .links__text {
    font-size: 14px;
    line-height: 24px;
    text-align: left;

    padding-bottom: 10px;
  }

  .links__image {
    height: auto;
    padding-bottom: 0;
  }

  .links__image--image1 {
    width: 147px;
  }

  .links__image--image2 {
    width: 162px;
  }

  .links__image--image3 {
    width: 155px;
  }

  .links__image--image4 {
    width: 127px;
  }

  .links__image--image5 {
    width: 125px;
  }

  .links__image--image6 {
    width: 163px;
  }

  .links__image--image7 {
    width: 130px;
  }

  .links__image--image8 {
    width: 175px;
  }




  .links__item--puprle {
    // display: none;
  }
}
