.cart {
  background-color: @background-white-color;
  box-shadow: 0 0 18px rgba(0,0,0,0.05);
  border-radius: 8px;

  padding: 20px 8px;

  margin-bottom: 20px;

  display: flex;
  flex-direction: column;
}

.cart--hide {
  display: none;
}

.cart__header {
  display: none;
}

.cart__wrpapper {
  padding-bottom: 20px;
}

.cart__go-back {
  position: relative;

  padding-left: 48px;

  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 23px;
  line-height: 33px;
  font-weight: normal;

  cursor: pointer;
}

.cart__go-back::before {
  position: absolute;
  top: 5px;
  left: 29px;
  content: "";
  background-image: url("../img/right-arrow.svg");
  height: 13px;
  width: 8px;
  background-position: left;
  background-repeat: no-repeat;
  background-size: contain;
}

.cart__checkout {
  align-self: center;

  background-color: @background-blue-color;
  border: none;
  border-radius: 10px;
  box-shadow: 0 3px 6px rgba(6,6,7,0.31);

  width: 90%;

  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 20px;
  line-height: 22px;
  font-weight: normal;
  text-transform: uppercase;

  padding-top: 14px;
  padding-bottom: 14px;

  cursor: pointer;
}

.cart__empty {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;

  padding-top: 100px;
  padding-bottom: 100px;
}

.cart__empty--hide {
  display: none;
}

@import "./shopping.less";


@media (min-width: @desktop-width) {
  .cart-wrapper {
    display: flex;
    flex-direction: row;
  }

  .cart {
    flex-grow: 1;

    min-height: calc(100vh - 366px);

    padding: 10px 19px;

    margin-right: 21px;
    margin-bottom: 0;
  }

  .cart--hide {
    display: flex;
  }

  .cart__wrpapper {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;

    padding-bottom: 32px;

    // padding-right: 12px;
  }

  .cart__header {
    display: block;

    font-family: "Franxurter Totally", "Arial", sans-serif;
    color: @dark-text-color;
    font-size: 23px;
    line-height: 33px;
    font-weight: normal;
  }

  .cart__go-back {
    position: relative;

    padding-left: 28px;

    font-family: "Franxurter Totally", "Arial", sans-serif;
    color: @dark-text-color;
    font-size: 23px;
    line-height: 33px;
    font-weight: normal;

    cursor: pointer;
  }

  .cart__go-back::before {
    top: 10px;
    left: 9px;
  }


  .cart__checkout {
    display: none;
  }
}
