@import "./select-count.less";

.dish {
  flex: 0 0 311px;
  box-sizing: border-box;

  display: flex;
  flex-direction: column;
  align-items: center;

  background-color: @background-white-color;
  border-radius: 16px;
  box-shadow: 0 0 18px rgba(0,0,0,0.05);

  padding-left: 17px;
  padding-right: 11px;
  padding-bottom: 17px;

  margin-right: 20px;
}

.dish-wrapper {
  display: flex;
  flex-direction: row;
  align-self: stretch;
  justify-content: space-between;
}

.dish-weight {
  background-color: @background-blue-color;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  box-shadow: 0 2px 2px rgba(0,0,0,0.16);

  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 15px;
  line-height: 15px;
  font-weight: normal;
  text-align: center;

  padding-top: 14px;
  padding-bottom: 11px;
  padding-left: 5px;
  padding-right: 5px;

  margin-left: 8px;

  min-width: 52px;
  box-sizing: border-box;
}

.dish-tag {
  background-color: @background-red-color;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  box-shadow: 0 2px 2px rgba(0,0,0,0.16);

  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @white-text-color;
  font-size: 15px;
  line-height: 15px;
  font-weight: normal;
  text-align: center;

  padding-top: 14px;
  padding-bottom: 11px;
  padding-left: 5px;
  padding-right: 5px;

  margin-right: 13px;
  min-width: 52px;
  box-sizing: border-box;
}

.dish__image {
  width: auto;
  height: 166px;

  padding-top: 13px;
  padding-bottom: 23px;
}

.dish__header {
  align-self: flex-start;
  padding-top: 14px;
  padding-bottom: 5px;

  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 28px;
  line-height: 20px;
  font-weight: normal;
}

.dish__description {
  font-size: 12px;
  line-height: 14px;
  align-self: flex-start;

  padding-bottom: 10px;
  padding-right: 20px;
}

.dish-wrapper--footer {
  margin-top: auto;
  align-items: center;
}

.dish__price {
  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 50px;
  line-height: 70px;
  font-weight: normal;
}

.dish__to-cart {
  background-color: @background-blue-color;
  border: none;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16);
  border-radius: 8px;

  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 18px;
  line-height: 31px;
  font-weight: normal;
  text-align: center;

  padding: 4px 21px;

  cursor: pointer;
}
