.select-count {
  display: flex;
  flex-direction: row;
  align-items: center;
  background: none;

  border-radius: 8px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16);

  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  input[type=number] {
    -moz-appearance: textfield;
  }

  *:focus {
    outline: none;
  }
}

.select-count__button {
  background-color: @background-blue-color;
  border: none;

  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 18px;
  line-height: 31px;
  font-weight: normal;
  text-align: center;

  cursor: pointer;
}

.select-count__button--dec {
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  padding-left: 12px;
}

.select-count__button--inc {
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  padding-right: 12px;
}

.select-count__input {
  background-color: @background-blue-color;
  border: none;
  width: 30px;
  padding: 0;

  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 26px;
  line-height: 33px;
  font-weight: normal;
  text-align: center;
}

.select-count--background-white .select-count__input,
.select-count--background-white .select-count__button  {
  background-color: #F7F7FC;
}

.select-count--background-white {
  border: 1px solid @background-blue-color;
  box-shadow: none;
}
