.order-navigator {
  padding-top: 30px;
  padding-bottom: 12px;
  overflow-y: auto;
}

.order-navigator__list {
  display: flex;
  flex-direction: row;

}

.order-navigator__item {
  border: 1px solid @background-blue-color;
  border-radius: 14px;
  background-color: @background-white-color;
  white-space: nowrap;

  margin-right: 10px;

  a {
    display: block;
    padding: 5px 25px;

    font-family: "Franxurter Totally", "Arial", sans-serif;
    color: @dark-text-color;
    font-size: 22px;
    line-height: 38px;
    font-weight: normal;

  }
  transition: 0.4s;
}

.order-navigator__item:hover {
  background-color: @background-blue-color;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16);
  transition: 0.4s;

  a {
    color: @white-text-color;
  }
}


@media (min-width: @desktop-width) {
  .order-navigator {
    padding-top: 41px;
  }

  .order-navigator__item a {
    font-size: 28px;
    line-height: 44px;
  }
}
