.error_message {
  z-index: 70;
  position: absolute;

  top: 70px;
  left: 0;
  right: 0;
  text-align: center;
  margin-left: auto;
  margin-right: auto;

  display: flex;
  align-items: center;
  justify-content: center;

}

.error_message__text {
  font-size: 14px;
  line-height: 22px;
  font-weight: bold;
  color: @white-text-color;

  border-radius: 10px;
  box-shadow: 0 6px 6px rgba(6,7,7,0.31);
  background-color: @background-red-color;

  padding: 15px 25px;
}
