.main-menu {
  display: flex;
  align-items: stretch;
}

.main-menu__item {
  margin-right: 26px;
  position: relative;

  display: flex;
  align-items: stretch;

  a {
    display: flex;
    align-items: center;
    padding-right: 10px;
    padding-left: 10px;

    font-size: 20px;
    line-height: 21px;
  }
}

.sub-menu {
  display: none;

  width: 300px;

  position: absolute;
  top: 100%;
  left: 0;
  z-index: 20;

  // padding: 9px 12px;
  background-color: @background-blue-color;

  a {
    color: @white-text-color;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 15px;
    padding-right: 55px;
  }
}

@keyframes bounce {
  0% {
    transform: scaleY(0);
  }
  100% {
    transform: scaleY(1);
  }
}

.main-menu__item:hover > .main-menu__sub-menu {//,
// .main-menu__item:focus-within > .main-menu__sub-menu {
  display: block;
  transform-origin: top;
  animation: bounce 0.3s;
}

