.order-details {
  background-color: @background-white-color;
  box-shadow: 0 0 18px rgba(0,0,0,0.05);
  border-radius: 8px;

  padding: 24px;
}

.order-details--hide {
  display: none;
}

.order-details__go-back {
  position: relative;

  padding-left: 22px;

  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 23px;
  line-height: 33px;
  font-weight: normal;

  cursor: pointer;
}

.order-details__go-back::before {
  position: absolute;
  top: 5px;
  left: 3px;
  content: "";
  background-image: url("../img/right-arrow.svg");
  height: 13px;
  width: 8px;
  background-position: left;
  background-repeat: no-repeat;
  background-size: contain;
}

.order-details__header {
  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 27px;
  line-height: 22px;
  font-weight: normal;

  padding-top: 17px;
  padding-bottom: 12px;
}




.order-details__form {
  display: flex;
  flex-direction: column;
}

.order-details__form-input {
  border: none;
  border-radius: 10px;
  background-color: #F7F7FC;

  font-size: 10px;
  line-height: 11px;

  padding: 14px;

  margin-bottom: 5px;
}

.order-details__form-input-comment {
  height: 128px;
  resize: none;
}

.order-details__form-input-wrapper {
  display: flex;
  align-items: flex-start;
}

.order-details__form-input-wrapper--checkbox {
  padding-top: 12px;
  padding-bottom: 50px;
}

.order-details__form-checkbox {
  margin-right: 11px;
  margin-top: 3px;
}

.order-details__form-checkbox-label,
.order-details__form-checkbox-label a {
  color: @light-text-color;
  font-size: 10px;
  line-height: 14px;
}

.order-details__form-checkbox-label a {
  text-decoration: underline;
}

.order-details__form-input-wrapper--total {
  padding-bottom: 15px;
  justify-content: space-between;
}

.order-details__form-text {
  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 24px;
  line-height: 33px;
  font-weight: normal;
}

.order-details__form-button {
  align-self: center;

  background-color: @background-blue-color;
  border: none;
  border-radius: 10px;
  box-shadow: 0 3px 6px rgba(6,6,7,0.31);

  width: 100%;

  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 20px;
  line-height: 22px;
  font-weight: normal;
  text-transform: uppercase;

  padding-top: 14px;
  padding-bottom: 14px;

  cursor: pointer;
}

@media (min-width: @desktop-width) {
  .order-details {
    padding: 24px;
    padding-top: 10px;

    flex: 0 0 289px;
    align-self: flex-start;
  }

  .order-details--hide {
    display: block;
  }

  .order-details__go-back {
    display: none;
  }

  .order-details__header {
    padding-top: 0;
  }
}
