.menu-group-navigator {
  padding-top: 25px;
}

.menu-group-navigator__header {
  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 30px;
  line-height: 33px;
  font-weight: normal;

  padding-bottom: 20px;
}

.menu-group-navigator__wrapper {
  overflow-y: auto;
}

.menu-group-navigator__list {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  padding-bottom: 12px;
}

@import "./dish.less";

@media (min-width: @desktop-width) {
  .menu-group-navigator {
    padding-top: 25px;
  }

  .menu-group-navigator__header {
    font-size: 48px;
    line-height: 40px;
  }
}
