.policy-window {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 60;

  background-color: rgba(123, 123, 123, 0.5);

  // display: flex;
  // justify-content: center;
  // align-items: center;

  *:focus {
    outline: none;
  }
}

.policy-window__overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(123, 123, 123, 0.5);
  z-index: 60;
}

.policy-window__wrapper {
  z-index: 61;
  position: absolute;
  margin: auto;
  // left: calc(10%);
  top: 10px; left: 0; bottom: 20px; right: 0;

  overflow-y: auto;

  box-sizing: border-box;
  background-color: @background-white-color;
  border-radius: 10px;
  box-shadow: 0 0 13px rgba(0,0,0,0.44);

  width: 90%;
  // height: 340px;
  max-width: 600px;

  padding-top: 10px;
  padding-bottom: 11px;
  padding-left: 34px;
  padding-right: 34px;

  // margin-left: auto;
  // margin-right: auto;
  outline: none;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.policy-window__text {
  padding-top: 10px;
  padding-bottom: 20px;
}

.policy-window__image {
  width: 80px;
  height: 80px;
}

.policy-window__header {
  padding-top: 10px;
  padding-bottom: 10px;
}

.policy-window__text-data {
  font-size: 14px;
  line-height: 24px;
  text-align: left;

  // padding-bottom: 30px;
}

.policy-window__button {
  margin-top: auto;
  background-color: @background-blue-color;
  border: none;
  border-radius: 10px;
  box-shadow: 0 3px 6px rgba(6,6,7,0.31);

  width: 100%;

  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @white-text-color;
  font-size: 20px;
  line-height: 22px;
  font-weight: normal;
  text-transform: uppercase;

  padding-top: 9px;
  padding-bottom: 9px;
  margin-bottom: 20px;

  cursor: pointer;
}


.policy-window__list {
  padding-left: 20px;
  padding-bottom: 10px;
}

.policy-window__list-item {
  list-style: disc;
}


@media (min-width: @desktop-width) {
  .policy-window__wrapper {
    width: 600px;

    // padding-left: 103px;
    // padding-right: 103px;
    // padding-top: 18px;
    // padding-bottom: 31px;


  }
}
