.head {
  background-color: #FFFFFF;
  border-radius: 16px;
  box-shadow: 0 0 18px rgba(0,0,0,0.05);
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
}


.head__left-panel {
  padding-top: 54px;
  padding-bottom: 27px;
  padding-left: 31px;
  padding-right: 31px;
}

.head__header {
  font-family: "Franxurter Totally", "Arial", sans-serif;
  color: @dark-text-color;
  font-size: 30px;
  line-height: 30px;
  font-weight: normal;

  padding-bottom: 12px;
  padding-right: 50px;
}

.head__text {
  color: @light-text-color;
  font-size: 24px;
  line-height: 33px;

  padding-bottom: 22px;
  margin-right: -25px;
}

.get-link-form {
  display: none;
}

.head__mobile-apps {
  padding-top: 12px;

  display: flex;
  flex-direction: row;
  justify-content: center;
}

.head__mobile-app:first-child {
  padding-right: 25px;
}


.head__right-panel {
  align-self: stretch;

  background-image: url("../img/logo-backround.png");

  // @media (min-resolution: @retina-dppx) {
  //   background-image: url("../img/logo-backround@2x.png");
  // }

  background-position: right;
  background-repeat: no-repeat;
  background-size: contain;

  display: flex;
  align-items: center;
  justify-content: center;
}

.head__logo-image {
  padding-top: 43px;
  padding-bottom: 96px;
}



@media (min-width: @desktop-width) {
  .head {
    flex-direction: row;
    justify-content: space-between;
  }

  .head__left-panel {
    padding-bottom: 38px;
  }

  .head__header {
    font-size: 46px;
    line-height: 42px;
  }

  .head__text {
    margin-right: 70px;

    padding-bottom: 16px;
  }

  .get-link-form {
    display: flex;
    flex-direction: column;
  }

  .get-link-form__title {
    font-family: "Franxurter Totally", "Arial", sans-serif;
    color: @dark-text-color;
    font-size: 17px;
    line-height: 17px;
    font-weight: normal;

    padding-bottom: 16px;
  }

  .get-link-form__input-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: stretch;
    padding-bottom: 25px;

    &:last-child {
      justify-content: flex-start;
      align-items: center;
    }
  }

  .get-link-form__input-email {
    color: @dark-text-color;
    font-size: 15px;
    line-height: 14px;
    border: 1px solid @background-blue-color;
    border-radius: 16px;

    flex-grow: 1;
    margin-right: 25px;
    padding-top: 12px;
    padding-bottom: 12px;
    padding-left: 14px;
    padding-right: 14px;
  }

  .get-link-form__button {
    font-family: "Franxurter Totally", "Arial", sans-serif;
    color: @dark-text-color;
    font-size: 18px;
    line-height: 18px;
    font-weight: normal;

    background-color: @background-blue-color;
    border: none;
    border-radius: 16px;

    padding: 14px 40px;
    margin-right: 90px;

    cursor: pointer;
  }

  .get-link-form__checkbox {
    margin-right: 6px;
  }

  .get-link-form__checkbox-label {
    color: @light-text-color;
    font-size: 15px;
    line-height: 18px;

    a {
      color: @light-text-color;
      font-size: 15px;
      line-height: 18px;
      text-decoration: underline;
    }
  }

  .head__mobile-apps {
    display: none;
  }



  .head__right-panel {
    align-self: stretch;

    background-position: right;
    background-size: auto;
  }

  .head__logo-image {
    padding-right: 208px;
    padding-left: 50px;
  }
}
